@import '../../scss/base/colors';

.greetingCard {
    display: flex;
    min-height: 10rem;
    justify-content: center;
    align-items: center;
    background: linear-gradient(to right top, $color-7,$color-4, $color-2);
    border-radius: 7px;
    overflow: hidden;
    position: relative;
    padding: 0;
    max-height: 25rem;
    

    .carousel-indicators {
        margin-bottom: .25rem;

        button {
            width: 5px;
            height:5px;
            border-radius: 100px;
            border: none,
        }
    }
    .carousel-control-next-icon, .carousel-control-prev-icon{
        width: 1rem;
        height: 1rem;
    }

    .captionC {
        top: 20%;
    }
}
.greeting {
    position: relative;
    padding-top: 1rem;
    padding-bottom: 1rem;

    &::before {
        position: absolute;
        content: '';
        height: 10rem;
        background: linear-gradient(rgba($color-5, 1)30%, rgba($color-4, 0.7)70%, rgba($color-3, 0) 100%);
        width: 100%;
        margin: 0;
        left: 0;
        top: 0;
    }
    .greetingText {
        font-size: 3rem;
        color: white;
        font-weight: 900;
        margin: 0;
    }
} 


.greetingSubText {
    position: absolute;
    font-weight: 900;
    font-size: 10rem;
    color: rgba(255 255 255 / 0.1);
    margin: 0;
}

.bannerImg {
    min-height: 10rem;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: baseline;

    img {
        width: 110%;
    }

    h3, p {
        margin: 0;
        color: black;
    }
}