.cartLink {
    text-decoration: none;
    color: white;
    opacity: 0.8;
    margin-right: 0.5rem;
}

.cartWidget {
    position: relative;
    

    .cartBadge {
        position: absolute;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 1px 2px;
        width: 22px;
        font-size: 0.75rem;
        background: rgba(red, 0.8);
        color: white;
        top: -5px;
        right: -15px;
        border-radius: 50px;
    }
}
